body {
  font-family: Arial, sans-serif;
}

.game {
  display: flex;
  justify-content: space-between;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(15, 40px);
  grid-gap: 2px;
  margin-bottom: 20px;
}

.square {
  width: 40px;
  height: 40px;
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
}

.status {
  margin-bottom: 10px;
}

.game-info {
  width: 200px;
}

ol, ul {
  list-style: none;
  padding-left: 0;
}

ol li button {
  margin-right: 10px;
}